import React, { useState, useEffect } from 'react';
import { Card } from 'primereact/card';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { Button } from 'primereact/button';
import { Dialog } from 'primereact/dialog';
import { InputText } from 'primereact/inputtext';
import { InputNumber } from 'primereact/inputnumber';
import { Message } from 'primereact/message';
import './HiltiProizvodnja.css';

interface Proizvodnja {
  _id?: string;
  datum: string;
  smena: string;
  proizvod: string;
  kolicina: number;
  radnici: string;
  napomena?: string;
}

const HiltiProizvodnja: React.FC = () => {
  const [proizvodnje, setProizvodnje] = useState<Proizvodnja[]>([]);
  const [displayDialog, setDisplayDialog] = useState(false);
  const [proizvodnja, setProizvodnja] = useState<Proizvodnja>({
    datum: new Date().toISOString().split('T')[0],
    smena: '',
    proizvod: '',
    kolicina: 0,
    radnici: '',
    napomena: ''
  });
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');

  useEffect(() => {
    loadProizvodnje();
  }, []);

  const loadProizvodnje = async () => {
    setLoading(true);
    try {
      // TODO: Implementirati API poziv za uitavanje proizvodnji
      // const response = await fetch('/api/hilti/proizvodnja');
      // const data = await response.json();
      // setProizvodnje(data);
      
      // Privremeni dummy data
      setProizvodnje([
        { _id: '1', datum: '2025-11-13', smena: 'Prva', proizvod: 'Hilti X-BTM', kolicina: 150, radnici: 'Tim A' },
        { _id: '2', datum: '2025-11-13', smena: 'Druga', proizvod: 'Hilti TE-YZ', kolicina: 120, radnici: 'Tim B' }
      ]);
    } catch (err) {
      setError('Greka pri uitavanju proizvodnji');
    } finally {
      setLoading(false);
    }
  };

  const openNew = () => {
    setProizvodnja({
      datum: new Date().toISOString().split('T')[0],
      smena: '',
      proizvod: '',
      kolicina: 0,
      radnici: '',
      napomena: ''
    });
    setDisplayDialog(true);
  };

  const hideDialog = () => {
    setDisplayDialog(false);
    setError('');
  };

  const saveProizvodnja = async () => {
    setLoading(true);
    setError('');
    try {
      // TODO: Implementirati API poziv za uvanje proizvodnje
      // const response = await fetch('/api/hilti/proizvodnja', {
      //   method: 'POST',
      //   headers: { 'Content-Type': 'application/json' },
      //   body: JSON.stringify(proizvodnja)
      // });
      
      setSuccess('Proizvodnja uspeno sauvana');
      hideDialog();
      loadProizvodnje();
    } catch (err) {
      setError('Greka pri uvanju proizvodnje');
    } finally {
      setLoading(false);
    }
  };

  const dialogFooter = (
    <div>
      <Button label="Otkai" icon="pi pi-times" onClick={hideDialog} className="p-button-text" />
      <Button label="Sauvaj" icon="pi pi-check" onClick={saveProizvodnja} loading={loading} />
    </div>
  );

  return (
    <div className="hilti-proizvodnja-container">
      <Card className="hilti-proizvodnja-card">
        <div className="card-header">
          <div className="header-content">
            <i className="pi pi-building text-4xl text-primary"></i>
            <div>
              <h1 className="card-title">Hilti Proizvodnja</h1>
              <p className="card-subtitle">Pregled i unos proizvodnih podataka</p>
            </div>
          </div>
          <Button 
            label="Nova proizvodnja" 
            icon="pi pi-plus" 
            onClick={openNew}
            className="p-button-success"
          />
        </div>

        {error && <Message severity="error" text={error} className="mb-3" />}
        {success && <Message severity="success" text={success} className="mb-3" />}

        <DataTable 
          value={proizvodnje} 
          loading={loading}
          paginator 
          rows={10}
          className="mt-3"
          emptyMessage="Nema podataka o proizvodnji"
        >
          <Column field="datum" header="Datum" sortable />
          <Column field="smena" header="Smena" sortable />
          <Column field="proizvod" header="Proizvod" sortable />
          <Column field="kolicina" header="Koliina" sortable />
          <Column field="radnici" header="Radnici" sortable />
          <Column field="napomena" header="Napomena" />
        </DataTable>
      </Card>

      <Dialog
        visible={displayDialog}
        style={{ width: '600px' }}
        header="Nova proizvodnja"
        modal
        footer={dialogFooter}
        onHide={hideDialog}
      >
        <div className="field">
          <label htmlFor="datum">Datum</label>
          <InputText
            id="datum"
            type="date"
            value={proizvodnja.datum}
            onChange={(e) => setProizvodnja({ ...proizvodnja, datum: e.target.value })}
            className="w-full"
          />
        </div>
        <div className="field">
          <label htmlFor="smena">Smena</label>
          <InputText
            id="smena"
            value={proizvodnja.smena}
            onChange={(e) => setProizvodnja({ ...proizvodnja, smena: e.target.value })}
            className="w-full"
            placeholder="npr. Prva, Druga, Trea"
          />
        </div>
        <div className="field">
          <label htmlFor="proizvod">Proizvod</label>
          <InputText
            id="proizvod"
            value={proizvodnja.proizvod}
            onChange={(e) => setProizvodnja({ ...proizvodnja, proizvod: e.target.value })}
            className="w-full"
            placeholder="Naziv proizvoda"
          />
        </div>
        <div className="field">
          <label htmlFor="kolicina">Koliina</label>
          <InputNumber
            id="kolicina"
            value={proizvodnja.kolicina}
            onValueChange={(e) => setProizvodnja({ ...proizvodnja, kolicina: e.value || 0 })}
            className="w-full"
            placeholder="Koliina"
          />
        </div>
        <div className="field">
          <label htmlFor="radnici">Radnici/Tim</label>
          <InputText
            id="radnici"
            value={proizvodnja.radnici}
            onChange={(e) => setProizvodnja({ ...proizvodnja, radnici: e.target.value })}
            className="w-full"
            placeholder="Radnici ili tim"
          />
        </div>
        <div className="field">
          <label htmlFor="napomena">Napomena</label>
          <InputText
            id="napomena"
            value={proizvodnja.napomena}
            onChange={(e) => setProizvodnja({ ...proizvodnja, napomena: e.target.value })}
            className="w-full"
            placeholder="Dodatne napomene"
          />
        </div>
      </Dialog>
    </div>
  );
};

export default HiltiProizvodnja;

